import React , {useState,useEffect,useContext ,Suspense } from 'react';


const Lazycom = (props) => {
    const [count , setCount] = useState(100);
    useEffect(() => {
       let timer = setInterval(() => {
          if (count > 0) {
            setCount((c) => {
                return c - 1;
            });
            clearInterval(timer)
          }
        }, 100);
      }, [count]);
    return (
        <div>
            {count} - {props.title}
        </div>
    )
}

const Lazycom1 = (props) => {
  const [count , setCount] = useState(100);
  useEffect(() => {
     let timer = setInterval(() => {
        if (count > 0) {
          setCount((c) => {
              console.log(c,props);
              return c - 1;
          });
          clearInterval(timer)
        }
      }, 100);
    }, [count]);
  return (
      <div>
          {count} - {props.title}
      </div>
  )
}
// export default Lazycom;   同时导出两个组件的情况下 需要新建一个文件来导出默认的组件
export{
  Lazycom,
  Lazycom1
}
